import React, { Component } from "react";
import User from "./User";

export class UserList extends Component {
	render() {

		const users = this.props.users.map(user => 
				<User key={user._links.self.href} user={user} />
			); 

		return (
			<table>
				<body>
					<tr>
						<th>First Name</th>
						<th>Last Name</th>
						<th>Email</th>
						<th>isActive</th>
					</tr>
					{users}
				</body>
			</table>
		);
	}
};

export default UserList;