import React, { Component } from "react";
import {Link} from 'react-router-dom';

export class NavBar extends Component {
	constructor(props){
		super(props);
		this.state = {
			showNav: false
		}

		this.toggleNav = this.toggleNav.bind(this)
	}

	toggleNav(){
		this.setState(state => ({
			showNav: !state.showNav
		}));
	}


	render() {

		return (

			<nav id="primary-nav">
				<ul>
					<Link to="/">
						<li>Home</li>
					</Link>
					<Link to="/about">
						<li>About</li>
					</Link>
					<Link to="/register">
						<li>Register</li>
					</Link>
					<Link to="/login">
						<li>Login</li>
					</Link>
					<Link to="/logout">
						<li>Logout</li>
					</Link>
					<Link to="/account">
						<li>Account</li>
					</Link>
					<Link to="/admin">
						<li>Admin</li>
					</Link>
					<Link to="/availabillity">
						<li>Availabillity</li>
					</Link>
				</ul>
			</nav>


		);
	}
};

export default NavBar