import React, { Component } from "react";
import axios from "axios";
import UserList from "./users/UserList";
import GetawayList from "./getaways/GetawayList";

export class Admin extends Component {
	constructor(props) {
		super(props);
		this.state = {
			users: [],
			getaways: []
		};
	}

	componentDidMount() {
		const usersApiEndpoint = "/api/users";
		const getawaysApiEndpoint = "/api/getaways"
		axios.get(usersApiEndpoint)
			.then((response) => this.setState({users: response.data._embedded.users}));
		axios.get(getawaysApiEndpoint)
						// .then((response) => console.log(response.data._embedded.getaways))
			.then((response) => this.setState({getaways: response.data._embedded.getaways}));	
	}

	render() {
		// const users = this.state.users.map(user => <p>{users.firstName}</p>);
		return (
			<div>

				<h1>Admin Page</h1>

				
				<div>
					<h3>Active Users Baby</h3>
					<UserList users={this.state.users}/>
				</div>
				<div>
					<h3>Getaways</h3>
					<GetawayList getaways={this.state.getaways} />
				</div>
			
			</div>
		);
	}
};

export default Admin;